.baiziDisplay {
  max-width: 60em;
  margin: 15px auto;
}

.baiziTitle {
  text-indent: 0.5em;
  letter-spacing: 0.3em;
}

.character {
  border: 1px solid black;
  width: 1.9em;
  height: 1.9em;
  display: inline-block;
  text-align: center;
  letter-spacing: -0.1em;
}

.punctuation {
  width: 0;
  position: absolute;
}

.jumbotron {
  width: 100vw;
  height: 40vh;
  background: url("/Baiziqian.jpg") no-repeat top center fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  text-shadow: 0px 4px 3px rgb(0, 0, 0, 0.4), 0px 8px 10px rgb(0, 0, 0, 0.2),
    0px 10px 10px rgb(0, 0, 0, 0.1);
}

.jumbotron hr {
  width: 30vw;
  border-top: 1px solid #f8f8f8;
  border-bottom: 2px solid rgba(0, 0, 0, 0.2);
}

.footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100vw;
  padding: 5px 5px 5px 5px;
  background-color: rgb(155, 92, 55);
  color: white;
}

.footer p {
  text-align: center;
}

#page-container {
  position: relative;
  min-height: 100vh;
}
#content-wrap {
  padding-bottom: 4rem;
  margin-top: 4em;
}

